.round-buttons {
  background: #0a52c1;
  width: 100%;
  display: block;
  margin: 0;
  padding: 10px;
  border-radius: 15px;
  color: white;
  text-align: center;
}

.round-buttons:hover {
  color: white;
  -webkit-transition: all ease-out 300ms;
  -o-transition: all ease-out 300ms;
  transition: all ease-out 300ms;
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}

.round-buttons.finished {
  background: #cf0b0d;
}



